const getUserData = (state) => {
  	return state.usersData;
}
const getUserToken = (state) => {
  	return state.token;
}

const getMsgList = (state) => {
  	return state.msgList;
}

const getT = (state) => {
  	return state.t;
}


export default {
	getUserData,
    getUserToken,
    getMsgList,
    getT
}



